body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#background {
  position: absolute;
  right: -5%;
  bottom: -5%;
  min-height: 110%;
  min-width: 110%;
}
.cta {
    font-family: 'Source Code Pro', monospace;
    font-size: 22px;
    height: 100%;
    padding-top: 382px;
}

.cta h1 {
    margin: 22px;
    font-size: 22px;
    font-weight: 500;
}

.cta b {
    font-size: 24px;
    font-weight: 400;
    font-family: 'Nidus Sans';
}

.cta button {
    position: relative;
    border: none;
    background-color: transparent;
    padding: 8px 8px 8px 40px;
    margin: 12px;
    font-size: 20px;
    font-family: 'Source Code Pro', monospace;
    border: 2px solid;
    cursor: pointer;
    outline: inherit;
    font-weight: 400;
    color: #1a2327;
}

.cta button:hover {
    border: 2px solid;
}

.cta button:active {
    color:  #ECEFF1;
    border-color: #ECEFF1;
}

svg {
    fill: #ECEFF1 !important;
}

.cta button img {
    fill: #ECEFF1 !important;
    position: absolute;
    left: 4px;
    top: 5px;
    width: 32px;
}


.cta button img.loading {
    -webkit-animation: rotate 2s infinite steps(4);
            animation: rotate 2s infinite steps(4);
}

@-webkit-keyframes rotate {
    100% {
        -webkit-transform: rotateZ(360deg);
                transform: rotateZ(360deg);
    }
}

@keyframes rotate {
    100% {
        -webkit-transform: rotateZ(360deg);
                transform: rotateZ(360deg);
    }
}
.location {
    font-size: 24px;
    font-family: 'Source Code Pro', monospace;
    letter-spacing: -.2px;
    z-index: 1000;
    cursor: pointer;
}

.location:hover {
    text-decoration: underline;
}
  
.rainfall {
    font-family: 'Roboto';
    font-size: 158px;
    line-height: 88px;
    padding: 36px 0 24px;
    font-weight: bold;
    z-index: 0;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    pointer-events: none;
}
  
.units {
    margin-left: -16px;
    font-family: 'Nidus Sans';
    font-size: 90px;
}
  
ul.intervals {
    font-family: 'Source Code Pro', monospace;
    font-weight: 500;
    margin: 0;
    list-style: none;
}

ul.intervals.wide {
    letter-spacing: 2px;
}
  
ul.intervals li {
    font-size: 28px;
    display: inline;
    padding-right: 24px;
    cursor: pointer;
}

ul.intervals li.heavy {
    font-size: 24px;
    font-weight: 700;
}
  
ul.intervals li:hover {
    text-decoration: underline;
}

ul.intervals li:active {
    color: #ECEFF1;
}

.location img {
    width: 24px;
    margin-bottom: -4px;
    padding-right: 8px;
    cursor: pointer;
}
header {
    position: fixed;
    top: 0;
    height: 90px;
    width: 100%;
    display: flex;
    align-items: center;
    padding: 16px 0 0 108px;
    font-family: 'Nidus Sans';
    cursor: pointer;
}

.logo {
    font-weight: bold;
    font-size: 40px;
}

ul.views {
    padding: 2px 0 0 24px;
    font-size: 24px;
    margin: 0;
    list-style: none;
}
  
ul.views li {
    display: inline;
    padding-right: 20px;
    cursor: pointer;
}

ul.views li:hover {
    text-decoration: underline;
}

ul.views li:active {
    color: #ECEFF1;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.about {
    padding: 184px 72px 0 56px;
    text-align: left;
    font-family: 'Source Code Pro', monospace;
    font-size: 22px;
    height: 100%;
    letter-spacing: -.1px;
}

.about b {
    font-size: 24px;
    font-weight: 400;
    font-family: 'Nidus Sans';
}

.about b span.tm {
    font-family: 'Source Code Pro', monospace;
    padding-left: 2px;
}

.about img {
    margin: 8px 0px 0px;
    width: 420px;
}
footer {
    position: fixed;
    bottom: 0;
    display: flex;
    /* border: 1px solid black; */
    width: 100%;
    height: 68px;
    align-items: center;
    font-family: 'Source Code Pro', monospace;
    padding: 0 0 0 80px;
    font-size: 26px;
}

footer .coordinate {
    padding: 0 20px 0 0;
    color: #1a2327;
}

footer img {
    left: 4px;
    top: 5px;
    width: 28px;
}
.app {
  text-align: center;
  font-family: 'Roboto', sans-serif;
  color: #263238;
  position: absolute;
  z-index: 2;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: .9;
}

@media (max-width: 599px) {
  
  .app {
    color: red !important;
  }
}

@media (min-width: 600px) { 
  .app {
    color: violet !important;
  }
}

@media (min-width: 900px) {
  .app {
    color: blue !important;
  }
}

@media (min-width: 1200px) {
  .app {
    color: green !important;
  }
}

@media (min-width: 1800px) {
  .app {
    color: orange !important;
  }
}
