.cta {
    font-family: 'Source Code Pro', monospace;
    font-size: 22px;
    height: 100%;
    padding-top: 382px;
}

.cta h1 {
    margin: 22px;
    font-size: 22px;
    font-weight: 500;
}

.cta b {
    font-size: 24px;
    font-weight: 400;
    font-family: 'Nidus Sans';
}

.cta button {
    position: relative;
    border: none;
    background-color: transparent;
    padding: 8px 8px 8px 40px;
    margin: 12px;
    font-size: 20px;
    font-family: 'Source Code Pro', monospace;
    border: 2px solid;
    cursor: pointer;
    outline: inherit;
    font-weight: 400;
    color: #1a2327;
}

.cta button:hover {
    border: 2px solid;
}

.cta button:active {
    color:  #ECEFF1;
    border-color: #ECEFF1;
}

svg {
    fill: #ECEFF1 !important;
}

.cta button img {
    fill: #ECEFF1 !important;
    position: absolute;
    left: 4px;
    top: 5px;
    width: 32px;
}


.cta button img.loading {
    animation: rotate 2s infinite steps(4);
}

@keyframes rotate {
    100% {
        transform: rotateZ(360deg);
    }
}